import React, { useState } from "react";
import VisibilitySensor from "react-visibility-sensor";
import { FaMapMarkedAlt, FaBookOpen } from "react-icons/fa";
import posed from "react-pose";
import styled from "styled-components";
import Modal from './Modal';
import "./css/ListBlock.css";

/*styled-components component*/
const PressBotton = styled.button`
    width: 80px;
    height: 30px;
    font-size: 0.8em;
    font-family: "Noto Sans TC", sans-serif;
    border: none;
    border-radius: 5px;
    background: rgb(162,162,162);
    margin:5px 5px 5px 5px;
    -webkit-transition: 0.1s all ease-out;
    -o-transition: 0.1s all ease-out;
    transition: 0.1s all ease-out;
    :hover {
    position: relative;
    top: -2px;
    background: rgb(255,255,255);
    -webkit-box-shadow: 2.5px 2.5px 5px 0px #000000;
            box-shadow: 2.5px 2.5px 5px 0px #000000;
    }
    :active {
    position: relative;
    top: 0px;
    background: rgb(54,54,54);
    -webkit-box-shadow: 0px 0px 0px 0px #000000;
            box-shadow: 0px 0px 0px 0px #000000;
    }
    :focus {
    outline:0;
    }
`;

/*react-pose component*/
/*start*/
const OperateDiv = posed.div({
    init: {
        scale: 0.5,
        opacity: 0,
        x: 0,
        y: 50
    },
    end: {
        scale: 1,
        opacity: 1,
        x: 0,
        y: 0,
        transition: { duration: 500 }
    }
});

function Action({ boxState, children }) {
    return <OperateDiv pose={boxState ? "end" : "init"}>{children}</OperateDiv>;
}

function BoxAnimate({ children }) {
    const [active, setAnimate] = useState(false);
    const onChange = isVisible => {
        if (isVisible) {
            setAnimate(true);
        }
    };
    return (
        <VisibilitySensor onChange={onChange} partialVisibility>
            {({ isVisible }) => {
                const content = isVisible || active;
                return (
                    <div>
                        <Action boxState={content}>{children}</Action>
                    </div>
                );
            }}
        </VisibilitySensor>
    );
}
/*end*/


class ListBlock extends React.Component {
    state = {
        showModal: 0,
    };
    //呼叫Modal
    getModal = value => {
        this.setState({ showModal: value });
    };
    hideModal = value => {
        this.setState({ showModal: 0 });
    };

    render() {
        return (
            <div className="mainStyle">
                {this.props.inputData.map((d, i) => {
                    const markerLink = ["https://www.google.com/maps?q="+d.coordinates[1]+","+d.coordinates[0]]
                    return <BoxAnimate>
                        <div key={i} className="boxStyle">
                            {d.Name}<br />
                            區域：{d.area}<br />
                            
                            <PressBotton onClick={() => this.getModal(d.Name)}>
                                <FaBookOpen/>開啟
                            </PressBotton>
                            <a href={markerLink} target='_blank' without rel="noopener noreferrer">
                                <PressBotton>
                                    <FaMapMarkedAlt/>導航！    
                                </PressBotton>
                            </a>
                        </div>
                        <Modal
                            show={this.state.showModal === d.Name}
                            onHide={() => this.hideModal(d.Name)}
                            name={d.Name}
                            text={d.description}
                            geoX={d.coordinates[0]}
                            geoY={d.coordinates[1]}
                        />
                    </BoxAnimate>
                })}
            </div>
        )
    }
}

export default ListBlock;