import React from "react";
import ReactDOM from "react-dom";
import AppRouter from "./AppRouter";
import Contact from "./Contact";
import Swal from 'sweetalert2';
import "./styles.css";

    const userAgent = navigator.userAgent;
    // console.log(userAgent)
    const isChrome = userAgent.indexOf("Chrome") > -1;
    const isFireFox = userAgent.indexOf("Firefox") > -1;
    const isSafari = userAgent.indexOf("Safari") > -1;
    // const isMaxthon = userAgent.indexOf("Maxthon") > -1;
    // const isIE = userAgent.indexOf("compatible") > -1 && userAgent.indexOf("MSIE") > -1 && !isOpera;
    // const isIE5 = isIE55 = isIE6 = isIE7 = isIE8 = false;
    console.log(userAgent)
    if(isChrome||isFireFox||isSafari){
      Swal.fire({
        width: `60%`,
        title: `歡迎使用拉麵找！`,
        html: `點我進入`,
        confirmButtonText: `確認`,
        showCloseButton: true,
        scrollbarPadding: true,
        confirmButtonColor: `rgb(108, 108, 108)`,
      })
    }else{
      Swal.fire({
        width: `90%`,
        title: `歡迎使用拉麵找！`,
        text: `版本`,
        html: `建議使用Chrome、FireFox及Safari等最新版本維持最佳體驗`,
        confirmButtonText: `確認`,
        showCloseButton: true,
        scrollbarPadding: true,
        confirmButtonColor: `rgb(108, 108, 108)`,
      })
    }



function App() {
  return (
    <div className="App">
      <AppRouter />
      <Contact />
    </div>
  );
}

const rootElement = document.getElementById("root");
ReactDOM.render(<App />, rootElement);