import React from "react";
import styled from "styled-components";

const BoxBase = styled.div`
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  width: 100%;
  height: 90vh;
  color: white;
`;
const DivBox = styled.div`
  width:40vw;
  height:60vh;
  border-width: 0px;
  border-style: none;
  border-radius: 20px;
  background-color: rgba(0, 0, 0, 0.7);
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  @media screen and (max-width: 800px) {
    width:90vw;
    height:60vh;
  }
`
const DivText = styled.div`
  text-align:left;
  padding:20px 20px 20px 20px;
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
`
const ALink = styled.a`
  text-align:center;
  color: rgb( 255, 255, 255);
`

function About() {
  return (
    <BoxBase>
      <DivBox>
        <h2>關於本站</h2>
        <DivText>
          這是一個查詢拉麵店的測試網站，會做這個網站的原因，是看到社團內有熱心的大大把全臺灣拉麵店資料統整到google mymap服務上，心裡起了一個念頭，想把這些資料結合網站呈現，於是花了一些時間建置了這個網站，裡面內容或code寫法有誤請多見諒，未來也會持續進行更新，謝謝各位。
        </DivText><br />
        <p><ALink href='https://bit.ly/TWRamenClubMAP' target='_blank'>【台灣拉麵愛好會專屬地圖】</ALink></p>
        <p><ALink href='https://facebook.com/groups/RamenTW/about/' target='_blank'>【台灣拉麵愛好會專屬社團】</ALink></p>
      </DivBox>
    </BoxBase >
  );
}

export default About;
