import React from 'react';
import styled from "styled-components";
import Filter from './Search/Filter';

const BoxBase = styled.div`
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  color: white;
`;

function Home() {
  return (
    <BoxBase>
      <Filter />
    </BoxBase>
  );
}

export default Home;
