import React from "react";
// import axios from 'axios';
import styled from 'styled-components';
import MapGL, {
    FullscreenControl,
    GeolocateControl,
    NavigationControl,
    ScaleControl,
    Marker,
} from "@urbica/react-map-gl";
import Cluster from "@urbica/react-map-gl-cluster";
import imgUrl from "./img/icon/ramen.png";
import Swal from 'sweetalert2';
import db from './firebase-db'

//https://sweetalert2.github.io/
//https://github.com/alex3165/react-mapbox-gl#readme
//https://www.google.com/maps/d/viewer?mid=1I8nWhKMX1j8I2bUkN4qN3-FSyFCCsCh7&ll=25.195591903593964%2C121.71210290570241&z=10
const MapToken = "pk.eyJ1Ijoiam9yZGFubWFwIiwiYSI6IjRUOVBuV28ifQ.ubu4SCJhADfVRbncXCXiPg";

const MapDiv = styled.div`
    display:block;
    width: 100%;
    height: 90vh;
`
const CircleButton = styled.div`
    background-color: red;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    opacity: 0.7;
    -webkit-transition: opacity 0.3s;
    -o-transition: opacity 0.3s;
    transition: opacity 0.3s;
    cursor: pointer;
    :hover {
        opacity: 1;
    }
`;

class MapBase extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            viewport: {
                latitude: 25.0429609,
                longitude: 121.5421425,
                zoom: 16
            },
            MapList: [],
        };
    }
    
    componentWillMount() {
        this.getRamenDB();
        this.getGeoLocation();
    }

    getRamenDB = () => {
        db.collection("ramenDB")
        .get()
        .then(d => {
            const tempdatalist = [];
            d.forEach(doc => {
                const getRecord = doc.data();
                tempdatalist.push(getRecord);
            });
            //  console.log('暫存資料：' + JSON.stringify(tempdatalist));
            this.setState({
                MapList: tempdatalist,
            });
        })
        .catch((error) => {
            console.log("Error getting document:", error);
        });
    }

    getGeoLocation = () =>{
        if (navigator.geolocation) {
            navigator.geolocation.getCurrentPosition(
                success => {
                    const getLat = success.coords.latitude;
                    const getLon = success.coords.longitude;
                    // console.log(positionLat + ", " + positionLon);
                    this.setState({
                        viewport: {
                            latitude: getLat.toFixed(6),
                            longitude: getLon.toFixed(6),
                            zoom: 16
                        },
                    });
                },
                error => {
                    alert(error.message);
                    this.setState({
                        viewport: {
                            latitude: 25.0429609,
                            longitude: 121.5421425,
                            zoom: 16
                        },
                    });
                },
                {
                    enableHighAccuracy: true,
                    timeout: 20000,
                    maximumAge: 1000
                }
            );
        }
    }

    getModal = (value) => {
        // console.log('單筆內容測試：'+JSON.stringify(value))
        // const myRegExp = />((http[s]{0,1}):\/\/bit\.ly\/.......)/g;
        // const description = value.description.replace(/>((http|https):\/\/[^<]+)/g, '><a href="$1">$1</a>')
        // const description = value.description.replace(/>((http[s]{0,1}):\/\/bit\.ly\/.......)/g, '><a href="$1">$1</a>');
        const descriptionReplace = value.description.replace(/>((http[s]{0,1}):(\/\/reurl\.cc\/.....|\/\/bit\.ly\/.......))/g, '><a href="$1" target="_blank">【點我進入】</a>');
        const markerLink = JSON.stringify("https://www.google.com/maps?q="+value.coordinates[1]+","+value.coordinates[0])
        console.log(markerLink)
        Swal.fire({
            width: `80 %`,
            title: value.Name,
            html: 
            descriptionReplace+'<br>'+
            '<br>'+
            markerLink.replace(/(^"((http|https):([^"]*))"*)/g, '<a href="$2" target="_blank">【開啟Google Map】</a>'),
            confirmButtonText: `確認`,
            showCloseButton: true,
            scrollbarPadding: true,
            confirmButtonColor: `rgb(108, 108, 108)`,
        })
    };


    render() {
        // console.log(JSON.stringify('MapList資料讀取：' + JSON.stringify(this.state.MapList)))
        //@urbica/react-map-gl-cluster
        const markerStyle = {
            width: "36px",
            height: "20px",
            color: "rgb(255,255,255)",
            background: "green",
            borderRadius: "5px",
            borderWidth: "2px",
            textAlign: "center"
        };
        const ClusterMarker = ({ longitude, latitude, pointCount }) => (
            <Marker longitude={longitude} latitude={latitude}>
                <div style={markerStyle}>{pointCount}</div>
            </Marker>
        );

        //add Marker to MapGL
        const addMarkers = this.state.MapList.map((d, i) => {
            return <Marker
                key={i}
                latitude={d.coordinates[1]}
                longitude={d.coordinates[0]}
            >

                <CircleButton
                    onClick={() => this.getModal(d)}
                >
                    <img
                        src={imgUrl}
                        style={{ width: "40px", height: "24px" }}
                        alt=""
                    />
                </CircleButton>
            </Marker>
        });

        return (
            <MapDiv>
                <MapGL
                    style={{ width: "100%", height: "100%" }}
                    mapStyle="mapbox://styles/mapbox/streets-v10"
                    accessToken={MapToken}
                    latitude={this.state.viewport.latitude}
                    longitude={this.state.viewport.longitude}
                    zoom={this.state.viewport.zoom}
                    onViewportChange={viewport => this.setState({ viewport })}
                >
                    <Cluster
                        radius={100}
                        extent={512}
                        nodeSize={64}
                        component={ClusterMarker}
                    >
                        {addMarkers}
                    </Cluster>

                    <FullscreenControl position="top-right" />
                    <NavigationControl showCompass showZoom position="top-left" />
                    <GeolocateControl
                        position="top-left"
                        positionOptions={{ enableHighAccuracy: true, timeout: 6000 }}
                        trackUserLocation="true"
                    />
                    <ScaleControl unit="metric" position="bottom-right" maxWidth="100" />
                </MapGL>
            </MapDiv>
        );
    }
}

export default MapBase;